/* eslint-disable react/prop-types */
import React from "react";
import { slide as Menu } from 'react-burger-menu';
import "../Menu.css";

function ValuesMenu(props) {
  return (
    <Menu right noOverlay disableAutoFocus isOpen width={375}>
      <label className="centered underlined" style={{padding: "1em 0"}}>
                      
            ,
                 .
      </label>

      <label className="centered large-print">
            
      </label>
      <label className="centered">
          cos() = {props.trigValues.cos}
      </label>
      <label className="centered">
          sin() = {props.trigValues.sin}
      </label>
      <label className="centered">
          tan() = {props.trigValues.tan}
      </label>
      <label className="centered">
          cot() = {props.trigValues.cot}
      </label>
      <label className="centered">
          sec() = {props.trigValues.sec}
      </label>
      <label className="centered">
          csc() = {props.trigValues.csc}
      </label>
    </Menu>
  );
}

export default ValuesMenu;
